.container {
	width: 50vw;
	height: 80vh;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	background-color: whitesmoke;
	color: grey;
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 10px 0 -5px whitesmoke, 0 10px 1px -4px rgba(0, 0, 0, 0.15),
		0 20px 0 -10px whitesmoke, 0 20px 1px -9px rgba(0, 0, 0, 0.15);
	position: relative;
	top: 100px;
	overflow: scroll;
}
.task {
	margin: 10px;
	padding: 10px;
	font-size: 20px;
	font-weight: bold;
	font-family: 'Concert One';
	letter-spacing: 1px;
}
input {
	width: 20vw;
	height: 30px;
	border: none;
	margin: 10px;
	padding: 5px;
}
ul {
	list-style: lower-alpha;
}
.fa-trash {
	color: rgb(18, 79, 150);
	font-size: 20px;
}
.fa-plus-square {
	color: rgb(18, 79, 150);
	font-size: 23px;
}
.btn {
	margin: 10px;
	border: none;
	background-color: whitesmoke;
}
.no-of-tasks {
	font-family: 'Concert One';
	font-size: 18px;
	text-align: center;
}
@media screen and (max-width: 430px) {
	.container {
		width: 80vw;
		height: 70vh;
		position: relative;
		top: 100px;
	}
	input {
		width: 70vw;
		height: 30px;
		border: none;
		border-radius: 10px;
		position: fixed;
		top: 100px;
	}
	.list {
		width: 95vw;
		height: 70vh;
		font-size: 18px;
		margin-top: 20px;
		padding: 5px;
		overflow: scroll;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}
	.btn {
		margin: 20px;
		border: none;
	}
	.fa-trash {
		color: rgb(18, 79, 150);
		font-size: 16px;
	}
}
